import { useState, useEffect } from 'react';
import { Role, Permission, AccessLevel } from '../types/interfaces';

export const usePermissions = () => {
    const [currentRole, setCurrentRole] = useState<Role | null>(null);
    const [permissions, setPermissions] = useState<Permission[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        loadPermissions();
    }, []);

    const loadPermissions = async () => {
        try {
            // Statt einen API-Aufruf zu machen, verwenden wir nur simulierte Daten
            // In einer Produktionsversion würde hier ein API-Aufruf stehen:
            // const response = await api.getCurrentUserRole();
            // setCurrentRole(response.data);
            
            // Simulierte Daten für die Entwicklung
            const simulatedRole: Role = {
                id: 1,
                name: 'Admin',
                description: 'Administrator mit allen Rechten',
                permissions: [
                    { page: 'users', accessLevel: 'both' as AccessLevel },
                    { page: 'roles', accessLevel: 'both' as AccessLevel }
                ]
            };
            
            setCurrentRole(simulatedRole);
            setPermissions(simulatedRole.permissions);
        } catch (err) {
            setError('Fehler beim Laden der Berechtigungen');
        } finally {
            setLoading(false);
        }
    };

    const hasPermission = (page: string, requiredLevel: AccessLevel = 'read'): boolean => {
        if (!permissions) return false;
        
        const permission = permissions.find(p => p.page === page);
        if (!permission) return false;

        switch (requiredLevel) {
            case 'read':
                return ['read', 'write', 'both'].includes(permission.accessLevel);
            case 'write':
                return ['write', 'both'].includes(permission.accessLevel);
            case 'both':
                return permission.accessLevel === 'both';
            default:
                return false;
        }
    };

    const isAdmin = (): boolean => {
        return currentRole?.name === 'Admin';
    };

    return {
        currentRole,
        permissions,
        loading,
        error,
        hasPermission,
        isAdmin
    };
};

export default usePermissions; 